



































export default {
    data() {
        return {
            tempSettings: 'metric',
            dayNight: 'day'
        }
    },
    methods: {
        temp(val: any): void {
            (<any>this).$emit('setTemp', (<any>this).tempSettings)
        },
        timesOfDay(val: any): void {
            (<any>this).$emit("timesOfDay", (<any>this).dayNight)
        },
    }
}
